import React, { useEffect } from 'react';
import { Field } from 'formik';
import { KntPhoneInputField, KntInputField } from 'shared/components/Fields';
import AsyncSelectJs from 'shared/components/Fields/AsyncSelectJs';
import { KntTwilioStyle, scrollIntoDomElement } from 'shared/services';
import { useAppState } from 'shared/state';
import { GoogleMap, Marker } from '@react-google-maps/api';
import { KntButton } from 'shared/components';
import isEmpty from 'lodash/isEmpty';
import { useBusinessProfile } from '../hooks';
import { BpImageUploader } from './BPImageUploader';
import { useAddressFieldUpdate } from '../../hooks';
import { useAuth } from '../../../../../modules/auth/hooks/useAuth';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { AiFillInfoCircle } from 'react-icons/ai';
import { useNotistack, useSwitch } from '../../../../../shared/hooks';
import { Button, FormControlLabel } from '../../../../../shared/components/Elements';
import { IconButton, Tooltip } from '@material-ui/core';
import { IOSSwitch } from 'shared/components/Elements/Fields/SwitchComp';

type BpFormElementsProps = {
	values: Record<string, unknown>;
	setFieldValue: any;
	isSubmitting: boolean;
	errors: Record<string, unknown>;
	setFieldError: any;
};

const containerStyle = {
	width: '100%',
	height: '400px'
};

export function BpFormElements(props: BpFormElementsProps) {
	const { isAdmin, companyId, isDarkMode } = useAppState();
	const { company, userData } = useAuth();
	const isOwner = company?.company?.owner?.id === userData.id;
	const { showNotification } = useNotistack();
	const { checked, handleChange } = useSwitch({ darkIcon: false });
	const { checked: darkLogoChecked, handleChange: handleChangeDarkLogo } = useSwitch({
		darkLogo: false
	});

	const {
		searchInputRef,
		calcedLat,
		calcedLng,
		onLoad,
		onUnmount,
		selected,
		onMarkerDrag,
		isLoaded,
		placeAddress,
		files,
		map
	} = useBusinessProfile(true);
	const { values, setFieldValue, isSubmitting, errors, setFieldError } = props;
	//Update Address when typing manually(not selecting from place search)
	useAddressFieldUpdate({ setFieldValue, searchInputRef });

	// setting address fields
	useEffect(() => {
		if (!isEmpty(placeAddress)) {
			setFieldValue('city', placeAddress.city);
			setFieldValue('address', placeAddress.plain());
			setFieldValue('state', placeAddress.state);
			setFieldValue('country', placeAddress.country);
			setFieldValue('zipcode', placeAddress.zip);
		}
	}, [placeAddress]);

	// setting location changes
	useEffect(() => {
		if (selected && 'lat' in selected && 'lng' in selected) {
			setFieldValue('lat', Number(selected?.lat).toFixed(9));
			setFieldValue('lng', Number(selected?.lng).toFixed(9));
		}
	}, [selected]);

	// setting files on change
	useEffect(() => {
		if (files.icon) {
			setFieldValue('icon', files.icon);
		}
		if (files.icon_dark) {
			setFieldValue('icon_dark', files.icon_dark);
		}
		if (files.logo) {
			setFieldValue('logo', files.logo);
		}
		if (files.logo_dark) {
			setFieldValue('logo_dark', files.logo_dark);
		}
	}, [files]);
	// scroll top to show invalid fields
	useEffect(() => {
		if (!isEmpty(errors)) {
			scrollIntoDomElement('business-profile-form');
		}
	}, [errors]);

	//reset sub_industry on industry reset or change
	useEffect(() => {
		setFieldValue('sub_industry', null);
	}, [values.industry]);

	//Just to fix scrolling top when there is error on submit
	useEffect(() => {
		if (isSubmitting && !isEmpty(errors)) {
			Object.keys(errors).forEach((field) => setFieldError(field, false));
		}
	}, [isSubmitting]);

	//Fix map center on component load
	useEffect(() => {
		if (isLoaded && map) {
			const center = new google.maps.LatLng(
				Number(values?.lat) as number,
				Number(values?.lng) as number
			);
			(map as GoogleMap).panTo(center);
		}
	}, [isLoaded, map]);

	return (
		<div className="w-100 mt-6 gap-x-11 lg:grid lg:grid-cols-2" id="business-profile-form">
			<div className="col-1">
				<Field
					label="Name"
					name="name"
					component={KntInputField}
					placeholder="Enter name here"
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
					disabled={!isOwner}
				/>
				<Field
					label="Website"
					name="website"
					component={KntInputField}
					placeholder="Enter Website here"
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>
				<div className="react-select-dropdown mb-4">
					<div className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText">
						<span>Industry</span>
						<sup>*</sup>
					</div>
					<Field
						component={AsyncSelectJs}
						customStylesProps={KntTwilioStyle}
						name="industry"
						placeholder="Industry name here"
						optionLabel="name"
						optionValue="id"
						isSearchable
						isClearable={false}
						loadOptionsKey="results"
						isDisabled={!isAdmin}
						loadOptionsUrl="/industries/"
						loadOptionsParams={(search: string) => ({
							extra: {
								search
							}
						})}
					/>
				</div>
				{values.industry ? (
					<div className="react-select-dropdown mb-4" id="industry">
						<div className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText">
							<span>Sub Industry</span>
							<sup>*</sup>
						</div>
						<Field
							component={AsyncSelectJs}
							customStylesProps={KntTwilioStyle}
							name="sub_industry"
							placeholder="Sub industry name here"
							optionLabel="name"
							optionValue="id"
							isSearchable
							isClearable={false}
							loadOptionsKey="results"
							isDisabled={!isAdmin}
							loadOptionsUrl={`/industries/${
								(values.industry as { id: number })?.id
							}/sub_industries/`}
							loadOptionsParams={(search: string) => ({
								extra: {
									search
								}
							})}
						/>
					</div>
				) : null}

				<Field
					label="About"
					name="about"
					type="textarea"
					component={KntInputField}
					placeholder="Enter Company description here"
					rows="5"
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>

				<FormControlLabel
					control={<IOSSwitch checked={checked.darkIcon} onChange={handleChange} name="darkIcon" />}
					label="Icon for dark mode"
					className="mb-4"
					classes={{ label: '!text-xs font-light text-kgrey-welcome' }}
				/>

				<div className="react-select-dropdown mb-4">
					<div className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText">
						<span>Business {checked.darkIcon ? 'dark' : ''} icon </span>
					</div>
					<BpImageUploader
						field={checked.darkIcon ? 'icon_dark' : 'icon'}
						setFieldValue={setFieldValue}
					/>
				</div>

				<FormControlLabel
					control={
						<IOSSwitch
							checked={darkLogoChecked.darkLogo}
							onChange={handleChangeDarkLogo}
							name="darkLogo"
						/>
					}
					label="Logo for dark mode"
					className="mb-4"
					classes={{ label: '!text-xs font-light text-kgrey-welcome' }}
				/>

				<div className="react-select-dropdown mb-4">
					<div className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText">
						<span>Business {darkLogoChecked.darkLogo ? 'dark' : ''} logo</span>
					</div>
					<BpImageUploader
						field={darkLogoChecked.darkLogo ? 'logo_dark' : 'logo'}
						setFieldValue={setFieldValue}
					/>
				</div>

				<h1 className="mb-4 text-2xl font-bold dark:text-white">Social connection</h1>
				<Field
					label="Facebook"
					name="facebook_url"
					component={KntInputField}
					placeholder="Enter facebook address here"
					disabled={!isAdmin}
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>
				<Field
					label="Twitter"
					name="twitter_url"
					component={KntInputField}
					placeholder="Enter twitter address here"
					disabled={!isAdmin}
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>
				<div className="profile__header-user-name ">
					<CopyToClipboard
						text={`https://t.me/KennektFeedbackBot?start=${companyId}`}
						onCopy={() => {
							showNotification({
								message: 'Feedback link has been copied',
								variant: 'success'
							});
						}}
					>
						<Button variant="contained" className="!mb-2">
							Copy Feedback Link
						</Button>
					</CopyToClipboard>
					<Tooltip
						title={
							<div className="text-sm">
								Please copy and share feedback link for your application developers. They need to
								add this link into support part of your own(custom) applications, such as mobile
								applications or web sites. When your clients click to related button or link in your
								application they will be directed related telegram messenger bot. By the way they
								can be connected to your Business from your own application or website. Now your
								clients can open cases inside KENNEKT and you can manage client and staff cases in
								one platform. Also please feel free with your further questions. We are glad to help
								you.
							</div>
						}
						placement="top"
						className="!mb-1 !ml-2"
					>
						<IconButton className={'mr-20 p-0'}>
							<AiFillInfoCircle />
						</IconButton>
					</Tooltip>
				</div>
			</div>
			<div className="col-2">
				<div className="mb-4">
					{/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
					<label
						htmlFor="address"
						className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText"
					>
						Address
					</label>
					<input
						id="xyz123"
						name="xyz123"
						autoComplete="xyz123"
						type="search"
						ref={searchInputRef}
						disabled={!isAdmin}
						className="w-full rounded-lg border border-transparent bg-kgrey-light-grey-1 px-6 py-3.5 text-xs font-medium placeholder-current placeholder-kgrey outline-none transition hover:border-primary focus:border-primary focus:shadow-lg dark:bg-kdark-lighter dark:text-white"
					/>
				</div>
				<Field
					label="City"
					name="city"
					component={KntInputField}
					placeholder="Enter City here"
					disabled={!isAdmin}
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>
				<Field
					label="State"
					name="state"
					component={KntInputField}
					disabled={!isAdmin}
					placeholder="Enter State here"
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>
				<Field
					label="Zipcode"
					name="zipcode"
					component={KntInputField}
					disabled={!isAdmin}
					placeholder="Enter zipcode here"
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>
				<Field
					label="Country"
					name="country"
					component={KntInputField}
					disabled={!isAdmin}
					placeholder="Enter country here"
					inputElementClassNames={'dark:bg-kdark-lighter font-medium dark:text-white'}
				/>
				<Field
					label="Phone number"
					name="phone"
					component={KntPhoneInputField}
					disabled={!isAdmin}
					placeholder="Enter company mobile phone number"
					enableSearch
					onChangeHandler={setFieldValue}
					className={'tel-input-profile'}
				/>
				<div className="react-select-dropdown mb-4">
					<div className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText">
						<span>Map</span>
					</div>
					{isLoaded ? (
						<GoogleMap
							mapContainerStyle={containerStyle}
							center={{
								lat: Number(calcedLat) || -3.745,
								lng: Number(calcedLng) || -38.523
							}}
							zoom={10}
							onLoad={onLoad}
							onUnmount={onUnmount}
							onClick={onMarkerDrag}
						>
							{/* Child components, such as markers, info windows, etc. */}
							<Marker
								draggable
								position={{
									lat: Number(selected?.lat) || Number(calcedLat),
									lng: Number(selected?.lng) || Number(calcedLng)
								}}
								onDragEnd={onMarkerDrag}
							/>
						</GoogleMap>
					) : (
						<p className="dark:text-white">
							There is problem with loading map or location has not been set.
						</p>
					)}
				</div>
				<div className="flex justify-start">
					<KntButton
						className={'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
						type="submit"
						isSubmitting={isSubmitting}
						isDisabled={!isAdmin || isSubmitting}
						color="primary"
					>
						Update
					</KntButton>
				</div>
			</div>
		</div>
	);
}
